import React from 'react'
import carusel1 from '../assets/images/carusel1.jpg'
import carusel2 from '../assets/images/carusel2.jpg'
import carusel3 from '../assets/images/carusel3.jpg'
import carusel4 from '../assets/images/carusel4.jpg'
import carusel5 from '../assets/images/carusel5.jpg'
import carusel6 from '../assets/images/carusel6.jpg'
import card1 from '../assets/images/card1.jpg'
import card2 from '../assets/images/card2.jpg'
import card3 from '../assets/images/card3.jpg'
import {Carousel, Card, CardDeck} from 'react-bootstrap'

export function PaginaPrincipal () {
    return (
        <div>
            <Carousel fade className="carrusel">
                <Carousel.Item className="carruselitem">
                    <img
                    className="d-block w-100"
                    src={carusel1}
                    alt="Primera Diapositiva"
                    />
                    <Carousel.Caption>
                    <h3>Desde la comodidad de tu casa</h3>
                    <p>Accede a mas de 1000 juegos, adquiriendo nuestro pase premium</p>
                    <a href='https://www.freepik.es/vectores/ordenador' target="_blank" rel="noreferrer">Vector de Ordenador creado por vectorpouch - www.freepik.es</a>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item className="carruselitem">
                    <img
                    className="d-block w-100"
                    src={carusel2}
                    alt="Second slide"
                    />

                    <Carousel.Caption>
                    <p>Videojuego del género multijugador de arena de batalla en línea y deporte electrónico el cual fue desarrollado por Riot Games</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item className="carruselitem">
                    <img
                    className="d-block w-100"
                    src={carusel3}
                    alt="Third slide"
                    />

                    <Carousel.Caption>
                    <p>Videojuego de disparos en primera persona multijugador gratuito desarrollado y publicado por Riot Games.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item className="carruselitem">
                    <img
                    className="d-block w-100"
                    src={carusel4}
                    alt="Third slide"
                    />

                    <Carousel.Caption>
                    <p>Videojuego multijugador de arena de batalla on-line desarrollado y publicado por Riot Games para Android, iOS, y consolas. El juego es una versión modificada del juego de PC, League of Legends.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item className="carruselitem">
                    <img
                    className="d-block w-100"
                    src={carusel5}
                    alt="Third slide"
                    />

                    <Carousel.Caption>
                    <p>Desarrollado por la empresa Epic Games, lanzado como diferentes paquetes de software que presentan diferentes modos de juego, pero que comparten el mismo motor de juego y mecánicas.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item className="carruselitem">
                    <img
                    className="d-block w-100"
                    src={carusel6}
                    alt="Third slide"
                    />

                    <Carousel.Caption>
                    <h3>Halo Reach</h3>
                    <p>Videojuego de disparos en primera persona desarrollado por Bungie y publicado por Microsoft Game Studios para la consola Xbox 360. Es el sexto videojuego de la saga Halo, así como su tercer videojuego Publicado.</p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>

            <CardDeck className="cardprin">
                <Card className="cardp">
                    <Card.Img variant="top" src={card1} />
                    <Card.Body>
                        <Card.Title>Call of Duty</Card.Title>
                        <Card.Text>
                            This is a wider card with supporting text below as a natural lead-in to
                            additional content. This content is a little bit longer.
                        </Card.Text>
                    </Card.Body>
                </Card>
                <Card className="cardp">
                    <Card.Img variant="top" src={card2} />
                    <Card.Body>
                        <Card.Title>Outriders</Card.Title>
                        <Card.Text>
                            This card has supporting text below as a natural lead-in to additional
                            content.{' '}
                        </Card.Text>
                    </Card.Body>
                </Card>
                <Card className="cardp">
                    <Card.Img variant="top" src={card3} />
                    <Card.Body>
                        <Card.Title>Forza Horizon 4</Card.Title>
                        <Card.Text>
                            This is a wider card with supporting text below as a natural lead-in to
                            additional content. This card has even longer content than the first to
                            show that equal height action.
                        </Card.Text>
                    </Card.Body>
                </Card>
            </CardDeck>
        </div>
    );
}
