import React from 'react'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { Navbar, Nav, Button, Form, FormControl } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css'
import { Login } from './Login';
import { PaginaPrincipal } from './PaginaPrincipal';
import { Juegos } from './Juegos';
import { SoporteTecnico } from './SoporteTecnico';
import { Contactenos } from './Contactenos';
import { Error404 } from './Error404';

const MenuPrincipal = () => {
    return (
        <Router>
            <Navbar bg="dark" variant="dark" expand="lg">
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto">
                    <Nav.Link href="/paginaprincipal/">Pagina Principal</Nav.Link>
                    <Nav.Link href="/login/">Iniciar Sesión</Nav.Link>
                    <Nav.Link href="/juegos/">Juegos</Nav.Link>
                    <Nav.Link href="/soportetecnico/">Soporte Tecnico</Nav.Link>
                    <Nav.Link href="/contactenos/">Contactenos</Nav.Link>
                </Nav>
                <Form inline>
                    <FormControl type="text" placeholder="Buscar" className="mr-sm-2" />
                    <Button variant="outline-secondary">Buscar</Button>
                </Form>
                </Navbar.Collapse>
            </Navbar>
            <Switch>
                <Route path="/login" exact component={ Login } />
                <Route path="/paginaprincipal" exact component={ PaginaPrincipal } />
                <Route path="/juegos" exact component={ Juegos } />
                <Route path="/soportetecnico" exact component={ SoporteTecnico } />
                <Route path="/contactenos" exact component={ Contactenos } />
                <Route component={ Error404 } />
            </Switch>
        </Router>
    );
}

export default MenuPrincipal;
