import React from 'react'
import { Form, Col, Button } from 'react-bootstrap';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import { CrearCuenta } from './CrearCuenta'
export const Login = () => {
    return (
        <div className="bodylogin">
            <Form className="Login">
                <div className="contenedorlogin">
                <Form.Label><strong>Accede a tu cuenta</strong></Form.Label>
                <Form.Row className="Filaslogin">
                    <Form.Group as={Col} controlId="formGroupEmail">
                        <Form.Label>Correo Electronico</Form.Label>
                        <Form.Control type="email" placeholder="ejemplo@mail.com" required/>
                    </Form.Group>
                </Form.Row>
                <Form.Row className="Filaslogin">
                    <Form.Group as={Col} controlId="formGroupContraseña">
                        <Form.Label>Contraseña</Form.Label>
                        <Form.Control type="password" placeholder="Digite la contraseña" required/>
                    </Form.Group>
                </Form.Row>
                <Form.Row className="Filaslogin">
                    <Router>
                        <Form.Group sm={12} as={Col}>
                            <Link to="/crearcuenta">
                            <Button variant="outline-primary" size="lg" block type="button">
                            Crear cuenta
                            </Button>
                            </Link>
                        </Form.Group>
                        <Switch>
                            <Route path="/crearcuenta" exact component={ CrearCuenta} />
                        </Switch>
                    </Router>
                    <Form.Group sm={12} as={Col}>
                        <Button variant="primary" size="lg" block type="submit">
                        Iniciar Sesión
                        </Button>
                    </Form.Group>  
                </Form.Row>
            </div>
            </Form>
        </div>
    );
}
