import React from 'react'
import { Col, Form, Button } from 'react-bootstrap';
export function CrearCuenta () {
    return (
        <div>
            <Form className="Cuenta">
                <Form.Label><strong>Crea tu cuenta</strong></Form.Label>
                <Form.Row>
                    <Form.Group as={Col} controlId="formGridNombre">
                        <Form.Label>Nombre</Form.Label>
                        <Form.Control placeholder="Digite su Nombre" required />
                    </Form.Group>
                    <Form.Group as={Col} controlId="formGridApellido">
                        <Form.Label>Apellido</Form.Label>
                        <Form.Control placeholder="Digite su Apellido" required/>
                    </Form.Group>
                </Form.Row>
                <Form.Row>
                    <Form.Group as={Col} controlId="formGroupEmail">
                        <Form.Label>Correo Electronico</Form.Label>
                        <Form.Control type="email" placeholder="ejemplo@mail.com" required/>
                    </Form.Group>
                </Form.Row>
                <Form.Row>
                    <Form.Group as={Col} controlId="formGroupContraseña">
                        <Form.Label>Contraseña</Form.Label>
                        <Form.Control type="password" placeholder="Digite la contraseña" required/>
                    </Form.Group>
                    <Form.Group as={Col} controlId="formGroupConfirmacion">
                        <Form.Label>Confirmación</Form.Label>
                        <Form.Control type="password" placeholder="Digite nuevamente la contraseña" required/>
                    </Form.Group>
                </Form.Row>
                <Form.Row>
                    <Form.Group as={Col}>
                        <Button variant="primary" size="lg" block type="submit">
                            Crear cuenta
                        </Button>
                    </Form.Group>
                </Form.Row>
            </Form>
        </div>
    );
}
