import React from 'react'
import atencion from '../assets/images/atencion.png'
import { Row, Col, Image, Container, Button } from 'react-bootstrap'
export function Contactenos () {
    return (
        <div>
            <Container className="contcontactenos">
                <Row>
                    <Col sm={6} md={6}>
                    <Image src={atencion} rounded />
                    </Col>
                    <Col sm={6} md={6}>
                        <h1>Ponte en contacto con nosotros</h1>
                        <p>Solicita una llama, chatea en linea y mucho mas.</p><br />
                        <p className="parrafo">Horas de contacto</p>
                        <Row>
                            <Col>
                               <strong>Soporte telefonico</strong> 
                               <p>De lunes a viernes: 8:00 Am a 6:00 Pm</p>
                               <p> Tel: 321480515 - 8905421</p>
                            </Col>
                            <Col>
                                <strong>Chat en web</strong> 
                                <p>De lunes a viernes: las 24 horas del dia</p>
                                <Button variant="outline-primary" size="md" block type="button">
                                    Chatear ahora
                                </Button>
                            </Col>
                        </Row>
                    </Col>

                </Row>
            </Container>
        </div>
    );
}